.submenu {
   position: absolute;
   background-color: transparent;
   visibility: hidden;
   opacity: 0;
   max-width: 190px;
   width: 100%;
   color: #000000;
   border-radius: 10px;
   margin: auto;
}
.submenu li a {
   display: block;
   color: #ffffff;
   background-color: #EF7F3F;
}
.submenu li a:hover {
   color: #000000;
   background-color: #ECE9CB;
   transition: background-color 2s;
}
.menu li:hover .submenu {
   visibility: visible;
   opacity: 1;
   transition: opacity 3s;
}



